.export-sequence-line .timeline {
  white-space: nowrap;
  padding-top: 0px;
  padding-right: 0;
  padding-left: 0;
  overflow-y: hidden;
  overflow-x: hidden;
  width: 85%;
  margin-right: auto;
  margin-left: auto;
  padding-bottom: 20px;
}
.export-sequence-line .timeline ol {
  font-size: 0;
  width: 100%;
  padding: 280px 0 0 0;
  transition: all 0.8s;
}
.export-sequence-line .timeline ol li {
  position: relative;
  display: inline-block;
  list-style-type: none;
  width: 180px;
  height: 4px;
  /* background: #fff; */
}
.export-sequence-line .timeline ol li:last-child {
  width: 0px;
  margin-right: 15px;
}
.export-sequence-line .timeline ol li:first-child {
  width: 0px;
  margin-left: 10px;
}
.export-sequence-line .timeline ol li:not(:first-child) {
  margin-left: 10px;
}
.export-sequence-line .timeline ol li:not(:last-child)::after {
  content: '';
  position: absolute;
  top: 50%;
  left: calc(100% + 1px);
  bottom: 0;
  width: 25px;
  height: 25px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border-radius: 50%;
  /* background: #F45B69; */
  border: 5px solid white;
  z-index: 1;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.2) /* border-width: 10px;
        border-color: white; */;
}
.export-sequence-line .timeline ol li:nth-last-child(2)::after {
  width: 20px;
  height: 20px;
  background: #a5a5a5;
  border: 4px solid white;
}
.export-sequence-line .timeline ol li > div {
  position: absolute;
  left: calc(100%);
  width: 175px;
  /* padding: 15px; */
  font-size: 1rem;
  white-space: normal;
  color: black;
  background: white;
  text-align: left;
}
.export-sequence-line .timeline ol li > div::before {
  content: '';
  position: absolute;
  top: 100%;
  left: 0;
  width: 15px;
  height: 15px;
}
.export-sequence-line .timeline ol li:nth-child(odd) > div {
  top: -30px;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.export-sequence-line .timeline ol li:nth-child(odd) > div::before {
  top: 100%;
}
.export-sequence-line .timeline ol li:nth-child(even) > div {
  /* top: calc(100% + 16px); */
  top: -30px;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.export-sequence-line .timeline ol li:nth-child(even) > div::before {
  top: 100%;
}
